import React, { useState } from 'react';
import useApi from '../hooks/useApi';
import "./Home.css";

const Home = props => {
    const [roomName, setRoomName] = useState("");
    const handleRoomChange = (e) => {
        setRoomName(e.target.value);
    };
    const [password, setPassword] = useState("");
    const handlePasswordChange = (e) => {
        setPassword(e.target.value);
    };
    const api = useApi();
    const createRoom = async (e) => {
        e.preventDefault();
        let room = await api("rooms", "POST", {
            name: roomName,
            password: password
        });
        if (room.error) {
            alert("Stanza duplicata");
        } else {
            alert("Stanza creata!");
        }
    };
    return (
        <div className="main">
            <div className="container">
                <h2>
                    Welcome to Pokemon Go drafter, the definitive tool to create a draft tournament with your friends.
            </h2>
                <form onSubmit={createRoom}>
                    <input placeholder="Room Name" type="text" value={roomName} onChange={handleRoomChange} />
                    <input placeholder="Password" type="password" value={password} onChange={handlePasswordChange} />
                    <button type="submit">Create Room</button>
                </form>
            </div>
        </div>
    )
};

export default Home;