import './App.css';
import {Home} from './views'
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";

function App() {
  return (
    <Router>
      <Switch>
        <Route path="/" exact component={Home}>
        </Route>
        <Route path="/rooms" exact>
          <div>Rooms</div>
        </Route>
        <Route path="/test" exact>
          <div>Test</div>
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
